<template>
  <v-bottom-sheet
    class="shopping-cart"
    v-model="sheet"
    max-width="500px"
    scrollable
  >
    <v-card min-height="100px" style="border-radius: 16px 16px 0 0">
      <v-card-title v-if="step === 0" class="headline font-weight-black py-4">
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              x-small
              :color="isOpenColor"
              style="margin-right: 8px"
              v-bind="attrs"
              v-on="on"
            >
              mdi-checkbox-blank-circle
            </v-icon>
          </template>
          <span v-if="isOpen === null">No data</span>
          <span v-else>{{
            isOpen
              ? "Prijmame nov objednvky"
              : "Zatvoren, neprijmame nov objednvky"
          }}</span>
        </v-tooltip>
        Objednvka
        <span class="ml-auto">
          <v-btn
            text
            rounded
            color="#0360ec"
            :disabled="
              !isOpen || order.length === 0 || minimalOrderValue > orderTotal
            "
            @click="step++"
          >
            {{
              minimalOrderValue > orderTotal
                ? `Minimum ${minimalOrderValue}`
                : "Pokraova"
            }}
          </v-btn>
        </span>
      </v-card-title>
      <v-card-title v-if="step === 1" class="headline font-weight-black py-4">
        <v-btn
          icon
          color="#0360ec"
          small
          @click="step--"
          style="margin-right: 8px"
        >
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
        Kontakt
        <span class="ml-auto">
          <v-btn
            text
            rounded
            color="#0360ec"
            :disabled="!valid"
            @click="step++"
          >
            Skontrolova
            <!-- <v-icon>mdi-arrow-right</v-icon> -->
          </v-btn>
        </span>
      </v-card-title>
      <v-card-title v-if="step === 2" class="headline font-weight-black py-4">
        <v-btn
          icon
          color="#0360ec"
          small
          @click="step--"
          style="margin-right: 8px"
        >
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
        Zhrnutie
        <span class="ml-auto">
          <v-btn
            text
            rounded
            color="#fc010e"
            :disabled="!agreementCheckbox"
            :loading="loading"
            @click="sendOrder"
          >
            {{ onlinePayment ? "Zaplati" : "Objedna" }}
          </v-btn>
        </span>
      </v-card-title>
      <v-card-title v-if="step === 3" class="headline font-weight-black py-4">
        {{ responseTitle }}
        <v-btn
          icon
          color="#0360ec"
          small
          @click="resetShoppingCart"
          class="ml-auto"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text v-if="step === 1" style="padding-bottom: 0px" class="pb-4">
        <!-- <v-chip-group
          v-model="formMode"
          mandatory
          center-active
          active-class="chip-group-active-class"
        >
          <v-chip filter value="pickup">Osobne</v-chip>
          <v-chip filter value="delivery">Dovies</v-chip>
        </v-chip-group> -->
        <v-chip-group
          v-model="delivery"
          mandatory
          center-active
          active-class="chip-group-active-class"
        >
          <v-chip v-if="deliveryOptions.takeaway.status" filter :value="false"
            >Vyzdvihn</v-chip
          >
          <v-chip v-if="deliveryOptions.delivery.status" filter :value="true"
            >Donka</v-chip
          >
        </v-chip-group>
        <v-form ref="form" v-model="valid">
          <v-text-field
            dense
            v-model="customer.name"
            label="Meno"
            rounded
            filled
            hide-details
            :rules="required"
            style="margin-bottom: 8px"
          ></v-text-field>
          <v-text-field
            dense
            v-model="customer.email"
            label="Email"
            rounded
            filled
            hide-details
            :rules="emailRules"
            style="margin-bottom: 8px"
          ></v-text-field>
          <v-text-field
            dense
            v-model="customer.mobile"
            label="Mobil"
            rounded
            filled
            hide-details
            :rules="required"
            style="margin-bottom: 8px"
          ></v-text-field>
          <v-text-field
            v-if="delivery"
            dense
            v-model="customer.address.street"
            label="Ulica"
            rounded
            filled
            hide-details
            :rules="required"
            style="margin-bottom: 8px"
          ></v-text-field>
          <v-text-field
            v-if="delivery"
            dense
            v-model="customer.address.city"
            label="Mesto"
            rounded
            filled
            hide-details
            :rules="required"
            style="margin-bottom: 8px"
          ></v-text-field>
          <v-select
            v-if="delivery"
            :items="deliveryOptions.delivery.zones"
            v-model="customer.address.deliveryZone"
            item-text="name"
            item-value="_id"
            dense
            label="Donkov zna"
            rounded
            filled
            hide-details
            :rules="required"
            style="margin-bottom: 8px"
          ></v-select>
          <!-- <v-text-field
            v-if="delivery"
            dense
            v-model="customer.address.zip"
            type="number"
            label="PS"
            rounded
            filled
            hide-details
            :rules="required"
            style="margin-bottom: 8px"
          ></v-text-field> -->
          <v-textarea
            label="Poznmka"
            v-model="note"
            rows="2"
            rounded
            filled
            hide-details
            style="margin-bottom: 16px"
          ></v-textarea>
          <v-chip-group
            v-model="onlinePayment"
            mandatory
            center-active
            class="paymentOptions"
            active-class="chip-group-active-class"
          >
            <span class="subheading mr-2" style="line-height: 32px !important"
              >Platba:</span
            >
            <v-chip
              v-if="paymentOptions.cashOnDelivery.status"
              filter
              :value="false"
              >Pri prevzat</v-chip
            >
            <v-chip
              v-if="paymentOptions.onlinePayments.status"
              filter
              :value="true"
              >Kartou online</v-chip
            >
          </v-chip-group>
          <v-checkbox
            v-model="saveCustomerCheckbox"
            label="Uloi daje pre budce objednvky"
            color="#0360ec"
            hide-details
          ></v-checkbox>
        </v-form>
      </v-card-text>
      <v-card-text v-if="step === 0" style="padding-bottom: 0px">
        <v-expansion-panels accordion readonly>
          <v-expansion-panel
            v-for="(dish, i) in order"
            :key="i"
            active-class="active"
          >
            <v-expansion-panel-header hide-actions>
              <v-col
                cols="1"
                sm="3"
                lg="3"
                class="hidden-xs-only image-thumbnail"
              >
                <v-img
                  v-if="dish.imageUrl"
                  class="mr-auto"
                  style="border-radius: 8px"
                  height="75"
                  width="100%"
                  :src="dish.imageUrl"
                ></v-img>
                <v-img
                  v-else
                  class="mr-auto"
                  style="border-radius: 8px"
                  height="75"
                  width="100%"
                  src="https://embeddable.piknik.cc/img/demo/placeholder.gif"
                ></v-img>
              </v-col>
              <v-col cols="9" sm="7">
                <div></div>
                <v-card-title style="padding-left: 0px; padding-top: 0px">
                  <v-list-item-title style="font-size: 20px"
                    ><span style="color: #0360ec; margin-right: 8px"
                      >{{ dish.quantity }}x</span
                    >{{ dish.name }}</v-list-item-title
                  >
                </v-card-title>
                <v-card-subtitle style="padding-left: 0px; padding-bottom: 8px">
                  <!-- <span class="d-none full-description">hello{{
                    dish.description
                  }}</span> -->
                  <span
                    v-if="optionsToString(dish) !== dish.description"
                    class="short-description"
                    >{{ optionsToString(dish) }}</span
                  >
                  <v-list-item-subtitle v-else class="short-description">{{
                    dish.description
                  }}</v-list-item-subtitle>
                </v-card-subtitle>
                <v-card-text style="padding-left: 0px; padding-bottom: 0px">
                  <span class="font-weight-bold" style="padding-bottom: 0px"
                    >{{ (dish.price * dish.quantity).toFixed(2) }}</span
                  >&#160;({{ dish.quantity }} x {{ dish.price }})
                </v-card-text>
              </v-col>
              <v-col cols="3" sm="2" style="text-align: end">
                <v-btn
                  icon
                  color="#0360ec"
                  small
                  @click.native.stop="$emit('add-dish', { dish })"
                >
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
                <v-btn
                  icon
                  color="#fc010e"
                  small
                  @click.native.stop="$emit('remove-dish', dish)"
                >
                  <v-icon>mdi-minus</v-icon>
                </v-btn>
              </v-col>
            </v-expansion-panel-header>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-card-text>
      <v-card-text v-if="step === 2" style="padding-bottom: 0px">
        <OrderSummary
          ref="orderSummary"
          :order="order"
          :delivery="delivery"
          :deliveryOptions="deliveryOptions"
          :onlinePayment="onlinePayment"
          :customer="customer"
          :note="note"
          :stripe="stripe"
          v-on:agreement-change="agreementCheckbox = $event"
          v-on:clear-order="$emit('clear-order')"
        />
      </v-card-text>
      <v-card-text v-if="step === 3" style="padding-bottom: 0px">
        <p style="color: rgba(0, 0, 0, 0.87)">
          {{ responseMessage }}
        </p>
      </v-card-text>
      <v-card-title
        v-if="order.length !== 0 && (step === 0 || step === 2)"
        class="font-weight-black py-4"
      >
        Spolu:<span style="color: #0360ec; margin-left: 8px; margin-right: 8px"
          >{{ orderTotalWithDelivery }}</span
        >
        <v-tooltip v-if="delivery" top>
          <template v-slot:activator="{ on, attrs }">
            <span v-bind="attrs" v-on="on">({{ deliveryPrice }} donka)</span>
          </template>
          <span
            >Cena donky pre donkov znu:
            {{
              deliveryOptions.delivery.zones.filter(
                (zone) => zone._id === this.customer.address.deliveryZone
              )[0].name
            }}.</span
          >
        </v-tooltip>
      </v-card-title>
    </v-card>
  </v-bottom-sheet>
</template>

<script>
import OrderSummary from "./OrderSummary";

export default {
  name: "ShoppingCart",
  props: [
    "restaurantId",
    "isOpen",
    "minimalOrderValue",
    "deliveryOptions",
    "paymentOptions",
    "status",
    "order",
    "orderTotal",
    "stripe",
  ],
  components: {
    OrderSummary,
  },
  data: () => ({
    delivery: false,
    onlinePayment: false,
    loading: false,
    customer: {
      name: null,
      email: null,
      mobile: null,
      address: {
        street: null,
        city: null,
        deliveryZone: null,
      },
    },
    note: "",
    step: 0,
    valid: false,
    emailRules: [
      (v) => !!v || "E-mail is required",
      (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
    ],
    required: [(v) => !!v || "Field is required"],
    saveCustomerCheckbox: true,
    agreementCheckbox: false,
    responseTitle: "",
    responseMessage: "",
  }),
  computed: {
    sheet: {
      get() {
        return this.status;
      },
      set() {
        this.$emit("close-shopping-cart");
      },
    },
    isOpenColor: function () {
      if (this.isOpen === true) return "#13e46d";
      else if (this.isOpen === false) return "#fc010e";
      else return "gray";
    },
    deliveryPrice: function () {
      try {
        return this.deliveryOptions.delivery.zones.filter(
          (zone) => zone._id === this.customer.address.deliveryZone
        )[0].price;
      } catch {
        return 0;
      }
    },
    orderTotalWithDelivery: function () {
      if (this.delivery)
        return Math.round((this.orderTotal + this.deliveryPrice) * 1e2) / 1e2;
      return this.orderTotal;
    },
  },
  created: function () {
    const params = new URLSearchParams(window.location.search);
    if (params.get("status") === "success") {
      this.responseTitle = "akujeme";
      this.responseMessage = `akujeme za Vau objednvku . ${params.get(
        "orderId"
      )}. O stave vaej objednvky vs budeme oskoro informova.`;
      this.step = 3;
    } else if (params.get("status") === "error") {
      this.responseTitle = "Prepte";
      this.responseMessage = `Vaa platba nebola sprvne spracovan.`;
      this.step = 3;
    }
  },
  mounted: function () {
    const customer = localStorage.getItem("customer");
    if (customer) {
      this.customer = JSON.parse(customer);
    }
  },
  methods: {
    optionsToString: function (dish) {
      const options = dish.options;
      let string = "+ ";
      options.forEach((option) => {
        option.values.forEach((value) => {
          if (value.quantity > 1)
            string += `${value.quantity}x ${value.name}, `;
          else string += `${value.name}, `;
        });
      });
      if (string.length === 2) return dish.description;
      return string.slice(0, -2);
    },
    sendOrder: async function () {
      this.loading = true;
      if (this.saveCustomerCheckbox)
        localStorage.setItem("customer", JSON.stringify(this.customer));
      const data = await this.$refs.orderSummary.sendOrder(this.restaurantId);
      if (data && data.id) {
        this.responseTitle = "akujeme";
        this.responseMessage = `akujeme za Vau objednvku . ${data.id}. O stave vaej objednvky vs budeme oskoro informova.`;
        this.step++;
      } else if (data && data.sessionId) {
        this.responseTitle = "Platba";
        this.responseMessage = `Prebieha presmerovanie...`;
        this.step++;
      } else {
        this.responseTitle = "Prepte";
        this.responseMessage = `Pri odosielan objednvky nastala chyba.`;
        this.step++;
      }
    },

    resetShoppingCart: function () {
      this.$emit("close-shopping-cart");
      this.step = 0;
      this.loading = false;
    },
  },
};
</script>

<style>
@media (max-width: 600px) {
  .v-bottom-sheet > * .v-expansion-panel-header {
    padding: 0 !important;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

@media (min-width: 600px) {
  .v-bottom-sheet > * .v-expansion-panel-header {
    padding: 0 0 !important;
  }
}

.shopping-cart {
  z-index: 1000;
}

.v-dialog {
  box-shadow: none !important;
}

.v-expansion-panel:first-child .col-sm-7 {
  padding-top: 0 !important;
}

.v-expansion-panel:first-child .image-thumbnail {
  padding-top: 0 !important;
}

.v-expansion-panel:last-child .col-sm-7 {
  padding-bottom: 0 !important;
}

.v-expansion-panel:last-child .image-thumbnail {
  padding-bottom: 0 !important;
}

.v-chip-group .v-slide-group__content {
  padding: 0 !important;
  margin-bottom: 8px;
}

.v-chip-group .v-chip {
  margin: 0 8px 0 0 !important;
}

.chip-group-active-class {
  color: #0360ec !important;
  caret-color: #0360ec !important;
}

.paymentOptions .v-slide-group__content {
  margin-bottom: 0 !important;
}
</style>