<template>
  <div class="dailyMenu" id="dailyMenu">
    <v-card-title class="headline font-weight-black pl-0 pb-0">
      Denn menu
    </v-card-title>
    <v-expansion-panels accordion>
      <v-expansion-panel v-for="(date, i) in data" :key="i">
        <v-expansion-panel-header>
          <div>
            <v-tooltip
              v-if="
                new Date().setHours(0, 0, 0, 0) ===
                new Date(date.date).setHours(0, 0, 0, 0)
              "
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-icon
                  x-small
                  :color="date.disabled ? '#fc010e' : '#13e46d'"
                  style="margin-right: 8px"
                  v-bind="attrs"
                  v-on="on"
                >
                  mdi-checkbox-blank-circle
                </v-icon>
              </template>
              <span
                >Objednvky od: {{ dailyMenuOrderingTime.from }} do
                {{ dailyMenuOrderingTime.to }}</span
              ></v-tooltip
            >{{ new Date(date.date).toLocaleDateString() }}
          </div>
        </v-expansion-panel-header>
        <v-expansion-panel-content class="hello">
          <v-expansion-panels accordion readonly>
            <v-expansion-panel
              v-for="(dish, i) in date.items"
              :key="i"
              active-class="active"
              :disabled="date.disabled"
            >
              <v-expansion-panel-header hide-actions class="py-0">
                <v-col cols="12" sm="9">
                  <v-card-title style="padding-left: 0px; padding-top: 0px"
                    ><span
                      v-if="
                        order.findIndex((item) => item._id === dish._id) > -1
                      "
                      style="color: #0360ec; margin-right: 8px"
                      >{{
                        order[order.findIndex((item) => item._id === dish._id)]
                          .quantity
                      }}x</span
                    >{{ dish.name }}</v-card-title
                  >
                  <v-card-subtitle style="padding-left: 0px"
                    ><span class="full-description">{{
                      dish.description
                    }}</span>
                    <v-list-item-subtitle class="short-description">{{
                      dish.description
                    }}</v-list-item-subtitle>
                    <v-card-text
                      style="padding: 0px"
                      v-if="dish.allergens && dish.allergens.length > 0"
                    >
                      Alergny: {{ dish.allergens.join(", ") }}
                    </v-card-text>
                  </v-card-subtitle>
                  <v-card-text style="padding-left: 0px; padding-bottom: 0px">
                    <span class="font-weight-bold" style="padding-bottom: 0px"
                      >{{ dish.price }}</span
                    >&#160;/{{ dish.amount }}
                    <v-btn
                      text
                      rounded
                      color="#0360ec"
                      small
                      :disabled="date.disabled"
                      @click.native.stop="$emit('add-dish', { dish })"
                    >
                      Prida
                    </v-btn>
                  </v-card-text>
                </v-col>
              </v-expansion-panel-header>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>

<script>
export default {
  name: "DailyMenu",
  props: ["dailyMenuOrderingTime", "data", "order"],
  data: () => ({}),
};
</script>

<style>
.image-thumbnail {
  padding-right: 12px !important;
}

.v-expansion-panel:before {
  box-shadow: none !important;
}

.full-description {
  display: none;
}

.dailyMenu .v-expansion-panel--active > .v-expansion-panel-header {
  min-height: 48px !important;
}

.dailyMenu .v-expansion-panel:last-child .col {
  padding-bottom: 12px !important;
}

.dailyMenu
  .v-expansion-panels
  .v-expansion-panel
  .v-expansion-panel:first-child
  .col-sm-9 {
  padding-top: 0 !important;
}

.dailyMenu .v-card__subtitle {
  padding-bottom: 8px !important;
}

.dailyMenu .v-expansion-panel-content__wrap {
  padding-bottom: 4px !important;
}
</style>